.site-header {
	/*position: absolute;*/
	z-index: 9999;
	width: 100%;
	background-color: $site-header--background-color;
	/*box-shadow: $box-shadow;*/

	.has-sticky-header & {
		position: fixed;
	}

	.page-boxed & {
		max-width: map_get($breakpoints, md);
	}

	> .wrap {
		display: flex;
		padding: 20px;
		flex-wrap: wrap;
		height: 100%;
		justify-content: space-between;
		align-items: center;

		@include flexfix;
	}
}
